#patientsListPage ul {
  list-style-type: none;
}
#patientsListPage li {
  margin-top: 10px;
  border-top: 1px solid gray;
  padding-bottom: 20px;
  min-height: 90px;
  overflow-y: hidden;
  padding: 0px;
  position: relative;
  text-align: justify;
  text-overflow: ellipsis;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#patientsListPage li .noResultsMessage {
  text-align: center;
}
#patientsListPage li .avatar {
  height: 80px;
  width: 80px;
  float: left;
}
#patientsListPage li .patientMetadata {
  position: relative;
  margin-left: 80px;
  padding-left: 20px;
}
#patientsListPage li:hover {
  color: teal;
  border-top: 1px solid teal !important;
  cursor: pointer;
}
#patientsListPage li:hover h2,
#patientsListPage li:hover h6,
#patientsListPage li:hover .barcode {
  color: teal;
}
#patientsListPage li.usersUnorderedList.notificationHidden {
  display: none;
  visibility: visible;
}
#patientsListPage .pull-right {
  position: absolute;
  top: 0px;
  right: 0px;
}
#patientsListPage .descriptionText {
  position: static;
  top: 0px;
  color: 'gray';
}
#patientsListPage .fadeAway {
  height: 10px;
  width: 100%;
  bottom: 0px;
  position: absolute;
  background: inherit;
}
#patientsListPage .addRecordIcon {
  text-align: center;
}
.off-screen {
  padding-right: 200px;
}
