#practitionerUpsertPage #practitionerMetaData {
  width: 100%;
}
#practitionerUpsertPage #practitionerImage {
  height: 126px;
  width: 126px;
  margin-left: 8px;
  background-color: #eeeeee;
  background: repeating-linear-gradient(135deg, #dddddd, #dddddd 10px, #eeeeee 10px, #eeeeee 20px);
  object-fit: cover;
  border: 1px solid gray;
}
#practitionerUpsertPage #practitionerMetaInputs {
  width: 550px;
  top: 0px;
  float: left;
}
#practitionerUpsertPage .pageFooter {
  height: 40px;
  padding-top: 5px;
  visibility: hidden;
}
#practitionerUpsertPage button {
  border: 0px solid white;
}
#practitionerUpsertPage button:hover,
#practitionerUpsertPage button:focus {
  color: maroon;
  outline: none;
}
#practitionerUpsertPage #saveRecordButton {
  position: absolute;
  left: 0px;
}
#practitionerUpsertPage #removeRecordButton {
  position: absolute;
  right: 0px;
}
