#practitionersTablePage {
  text-align: left;
}
#practitionersTablePage table {
  width: 100%;
  border-collapse: collapse;
}
#practitionersTablePage th,
#practitionersTablePage td {
  height: 30px;
}
#practitionersTablePage tbody tr {
  border-top: 1px solid black;
}
#practitionersTablePage tbody tr:hover {
  background-color: #eeeeee;
}
#practitionersTablePage .gray {
  color: lightgray;
}
#practitionersTablePage .addRecordIcon {
  text-align: center;
}
#practitionersTablePage .created {
  width: 200px;
}
