#patientsTablePage {
  text-align: left;
}
#patientsTablePage table {
  width: 100%;
  border-collapse: collapse;
}
#patientsTablePage th,
#patientsTablePage td {
  height: 30px;
}
#patientsTablePage tbody tr {
  border-top: 1px solid black;
}
#patientsTablePage tbody tr:hover {
  background-color: #eeeeee;
}
#patientsTablePage .gray {
  color: lightgray;
}
#patientsTablePage .addRecordIcon {
  text-align: center;
}
#patientsTablePage .created {
  width: 200px;
}
