#practitionersListPage ul {
  list-style-type: none;
}
#practitionersListPage li {
  margin-top: 10px;
  border-top: 1px solid gray;
  padding-bottom: 20px;
  min-height: 90px;
  overflow-y: hidden;
  padding: 0px;
  position: relative;
  text-align: justify;
  text-overflow: ellipsis;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
#practitionersListPage li .noResultsMessage {
  text-align: center;
}
#practitionersListPage li:hover {
  color: maroon;
  border-top: 1px solid maroon !important;
  cursor: pointer;
}
#practitionersListPage li.usersUnorderedList.notificationHidden {
  display: none;
  visibility: visible;
}
#practitionersListPage .pull-right {
  position: absolute;
  top: 0px;
  right: 0px;
}
#practitionersListPage .descriptionText {
  position: absolute;
  top: 25px;
}
#practitionersListPage .fadeAway {
  height: 10px;
  width: 100%;
  bottom: 0px;
  position: absolute;
  background: inherit;
}
#practitionersListPage .addRecordIcon {
  text-align: center;
}
.off-screen {
  padding-right: 200px;
}
